<template>
  <div ref="container" class="keen-slider first">
    <div
        v-for="(product, i) in props.products" :key="i"
        class="keen-slider__slide">
        <ProductCardElement :product="product" />
    </div>
  </div>
</template>

<script setup>
import {defineProps} from "vue";
import { useKeenSlider } from "keen-slider/vue.es";
import "keen-slider/keen-slider.min.css";
import ProductCardElement from "@/elements/ProductCardElement"

const [container] = useKeenSlider({
  loop: false,
  mode: "free",
  slides: {
    perView: 1.9,
    spacing: 1,
  },
  selector: '.first > .keen-slider__slide'
});

const props = defineProps(['products'])



</script>

