<template>
  <div>
    <label for="input" class="input-label">
      <div>
        {{props.label}}
      </div>
      <div class="input-label-error-text">
        {{ props.errorText }}
      </div>
    </label>
    <div id="input" class="right-icon-input" :style="border" >
      <input
          :value="modelValue"
          @input="$emit('update:modelValue', $event.target.value)"
          :type="type" />

      <div v-if="props.type === 'password'">
        <img
            @click="changeType"
            src="@/assets/img/eye.svg">
      </div>
    </div>
  </div>
</template>

<script setup>

import {defineProps, defineEmits, ref, watch} from "vue";

const props = defineProps(['modelValue', 'label', 'type', 'errorText'])
defineEmits(['update:modelValue'])

const border = ref('')

if(props.errorText!==undefined && props.errorText !== ''){
  border.value = 'border-color: #F93939;'
}

// eslint-disable-next-line no-unused-vars
watch(()=>props.errorText, async (newText, oldText) => {
  console.log('text changed')
  if(!(props.errorText===undefined || props.errorText === '')){
    border.value = 'border-color: #F93939;'
  }else {
    border.value = ''
  }
})

console.log(props.errorText)



const changeType = ()=>{
  if(type.value === 'password'){
    type.value = 'text'
  }else{
    type.value = 'password'
  }
}

const type = ref('text')

if(props.type==='password'){
  type.value = 'password'
}



</script>

<style scoped>

.input-label-error-text{
  color: #FE4747;
  text-align: right;
}

.input-label{
  display: flex;
}

input[type=password]::-ms-reveal,
input[type=password]::-ms-clear
{
  display: none;
}

.right-icon-input{
  border: 1px solid #C4C4C4;
  padding: 10px;
  display: flex;
}

#input{
  height: 40px;
  margin: 0;
  padding: 0 0 0 10px;
}

img{
  margin-top: 12px;
  margin-right: 5px;
}

img:hover{
  cursor: pointer;
}

div{
  text-align: left;
}

label{
  height: 13px;
  top: 3.5px;
  font-family: Open Sans,serif;
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  line-height: 13px;
  letter-spacing: 0em;
  text-align: left;
  color: #757577;
}

input{
  font-family: 'Open Sans',serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  border: none;
  width: 330px;
  letter-spacing: 0em;
  text-align: left;

}

input:focus{
  border: none;
  outline: none;
}


</style>