<template>
  <div class="container">
    <div>
      <img class="product-img" :src="props.product.imgUrl" />
    </div>
    <div>
      <div>
        {{props.product.name}}
      </div>
      <div>
        {{props.product.price}} ₽
      </div>
    </div>
    <div>
      <input type="checkbox" @change="change" />
    </div>
  </div>
</template>

<script setup>
import {defineProps, ref} from "vue";

const props = defineProps(['product', 'change'])

const checkbox = ref(false)

const change = () => {
  checkbox.value = !checkbox.value
  if (props.change !== null && props.change !== undefined) {
    props.change(checkbox.value, props.product.id)
  }
}

</script>

<style scoped>

.product-img{
  width: 75px;
  height: 75px;
}

.container{
  display: grid;
  grid-template-columns: 80px 1fr 23px;
}


</style>