import {createStore} from 'vuex'

export default createStore({
    state: {
        user: {
            loaded: false,
            email: '',
            code: '',
            token: ''
        },
        config: {
            isMobile: false,
            container_size: 0
        },
        basket: [],
        favourite: [],
        sections: {
            chairs: 'Стулья',
            tables: 'Столы',
            floors: 'Полы',
            walls: 'Стены',
            lighting: 'Освещение',
            soft_furniture: 'Мягкая мебель',
            storages: 'Хранилище'
        },

        default_search_filters: {


            // price order
            order_by_asc: true,
            // loft minimalism hai-tek
            style: {
                minimalism: false,
                loft: false,
                hai_tek: false
            },
            color: {
                red: false,
                yellow: false,
                grey: false,
                black: false,
                blue: false,
                turquoise: false,
            },
            start_price: 0,
            end_price: 100000,
            start_width: 35,
            end_width: 3500,
            start_deep: 45,
            end_deep: 1500,
            start_height: 15,
            end_height: 2670,
            start_weight: 1,
            end_weight: 67,
        },
        search_filters: {


            // price order
            order_by_asc: true,
            // loft minimalism hai-tek
            style: {
                minimalism: false,
                loft: false,
                hai_tek: false
            },
            color: {
                red: false,
                yellow: false,
                grey: false,
                black: false,
                blue: false,
                turquoise: false,
            },
            start_price: 0,
            end_price: 100000,
            start_width: 35,
            end_width: 3500,
            start_deep: 45,
            end_deep: 1500,
            start_height: 15,
            end_height: 2670,
            start_weight: 1,
            end_weight: 67,
        },

        previousPageData: {
            url: '',
            data: {

            }
        }
    },
    getters: {
        getUser(state) {
            if (!state.user.loaded) {
                state.user = JSON.parse(localStorage.getItem('user'))
                state.user.loaded = true
            }

            return state.user
        }
    },
    mutations: {

        setFavouriteProducts(state, products){
          state.favourite = products
        },

        updateFavouriteCategories(state, productIds, category){
          for (let i = 0; i < state.favourite.length; i++){

              if(productIds.includes(state.favourite[i].id)){
                  state.favourite[i].userCategory = category
              }
          }
        },

        setPreviousPageData(state, url, data){
            state.previousPageData.url = url
            state.previousPageData.data = data
        },
        updateFilters(state, filters){
          state.search_filters = filters
        },
        clearFavourite(state) {
            state.favourite.splice(0)
        },
        removeFromFavourite(state, productId) {
            let position = -1
            for (let i = 0; i < state.favourite.length; i++) {
                if (state.favourite[i].id === productId) {
                    position = i
                    break
                }
            }

            if (position !== -1) {
                state.favourite.splice(position, 1)
            } else {
                console.log('element not found error!!!')
            }
        },
        addToFavourite(state, product) {
            state.favourite.push(product)
        },
        clearBasket(state) {
            state.basket.splice(0)
        },
        removeFromBasket(state, productId) {
            let position = -1
            for (let i = 0; i < state.basket.length; i++) {
                if (state.basket[i].id === productId) {
                    position = i
                    break
                }
            }

            if (position !== -1) {
                state.basket.splice(position, 1)
            } else {
                console.log('element not found error!!!')
            }
        },
        // also used to update count + or -
        addToBasket(state, product) {

            for(let i = 0; i < state.basket.length; i++){
                if(state.basket[i].id === product.id){
                    state.basket[i].addingCount = product.addingCount
                    return
                }
            }

            state.basket.push(product)
        },
        setEmail(state, email) {
            state.user.email = email;
            localStorage.setItem('user', JSON.stringify(state.user))
        },
        setCode(state, code) {
            state.user.code = code;
            localStorage.setItem('user', JSON.stringify(state.user))
        },
        setIsMobile(state, isMobile) {
            state.config.isMobile = isMobile;
        },
        setToken(state, token) {
            state.user.token = token
            localStorage.setItem('user', JSON.stringify(state.user))
        }
    },
    actions: {
        updateBasket({commit}, product){
            if(product.addingCount === 0){
                commit('removeFromBasket', product.id)

            }else{
                commit('addToBasket', product)
            }
        }
    },
    modules: {}
})
