<template>
  <div>
    <div v-for="(path, i) in paths" :key="i">
      <router-link :to="path">
        {{path}}
      </router-link>
    </div>

  </div>
</template>

<script setup>
import router from "@/router";


const paths = router.getRoutes().map(x=>x.path)

</script>

