<template>
  <div>
    <div v-for="(product, i) in props.products" :key="i">
      <ProductCheckBoxElement :change="change"  :product="product" />
    </div>

    <BottomFixedPanelElement :height="70">
      <button class="app-blue-btn apply-btn" @click="addToCategory">
        Добавить выбранное в категорию
      </button>
    </BottomFixedPanelElement>
  </div>
</template>


<script setup>
import {defineProps, reactive} from "vue";
import ProductCheckBoxElement from "@/elements/ProductCheckBoxElement";
import BottomFixedPanelElement from "@/elements/BottomFixedPanelElement";
import store from "@/store";


const props = defineProps(['products', 'category'])

const selectedProductIds = reactive([])

const addToCategory = ()=>{
  store.commit('updateFavouriteCategories', selectedProductIds, props.category)
}

const change = (val,id)=>{

  if(val){
    selectedProductIds.push(id)
  }else{
    selectedProductIds.splice(selectedProductIds.indexOf(id), 1)
  }

}

</script>

<style src="../assets/styles/buttons.css"></style>
<style scoped>

.apply-btn{
  margin: 15px ;
  height: 40px;
  width: 1fr;
}

</style>